import React, { useState } from 'react';
import Header from 'Components/Head';
import LoginModal from 'Modal/loginModal';
import SignupModal from 'Modal/signupModal';
import Simplify from './Simplify';
import Features from './Features';
import SignAndRefer from './SignAndRefer';
import Footer from './Footer';
import './index.scss';
const Dashboard = () => {
  const [loginModalShow, setLoginModalShow] = useState(false);
  const [signupModalShow, setSignupModalShow] = useState(false);
  return (
    <div>
      <Header
        setLoginModalShow={setLoginModalShow}
        setSignupModalShow={setSignupModalShow}
      />
      <div className='Dashboard'>
        <Simplify />
        <Features />
        <SignAndRefer signupModalShow={signupModalShow} setSignupModalShow={setSignupModalShow} />
      </div>
      <Footer />
      <>
        <LoginModal show={loginModalShow} onHide={() => setLoginModalShow(false)} />
        <SignupModal show={signupModalShow} onHide={() => setSignupModalShow(false)} />
      </>
    </div >
  )
}

export default Dashboard