import React, { useEffect, useState } from 'react';
import { AiOutlineAlignLeft } from "react-icons/ai";
import EllipsisText from "react-ellipsis-text";
import { useWeb3React } from "@web3-react/core";

import AccountDetail from "Modal/accountDetailModal";
import ChooseNetwork from "Modal/chooseNetworkModal";

import addressIcon from "assets/image/addressIcon.png";
import etherIcon from "assets/image/ethereum.png";
import polygonIcon from "assets/image/polygon.png";
import bscIcon from 'assets/image/bsc.png';
import avalancheIcon from "assets/image/avalanche.png";

const AddressSVG = ({ size }) => (
  <img src={addressIcon} width={size} height={size} alt="Address" />
);

const networkData = [
  { logo: <img src={etherIcon} width={20} height={20} alt="Ether" className='me-2' />, name: "Ethereum", chainId: "0x1" },
  { logo: <img src={polygonIcon} width={20} height={20} alt="Polygon" className='me-2' />, name: "Polygon", chainId: "0x89" },
  { logo: <img src={avalancheIcon} width={20} height={20} alt="Avalanche" className='me-2' />, name: "Avalanche", chainId: "0xa86a" },
  { logo: <img src={etherIcon} width={20} height={20} alt="Ropeten" className='me-1' />, name: "Ropsten", chainId: "0x3" },
  { logo: <img src={etherIcon} width={20} height={20} alt="Rinkby" className='me-1' />, name: "Rinkby", chainId: "0x4" },
  { logo: <img src={etherIcon} width={20} height={20} alt="Goerli" className='me-1' />, name: "Goerli", chainId: "0x5" },
  { logo: <img src={etherIcon} width={20} height={20} alt="Kovan" className='me-1' />, name: "Kovan", chainId: "0x2a" },
  { logo: <img src={bscIcon} width={20} height={20} alt="BSC" className='me-1' />, name: "Binance Smart Chain", chainId: "0x38" },
];

const AfterLogin = ({ account }) => {
  const { library, chainId } = useWeb3React();
  const [network, setNetwork] = useState();
  const [accountDetailModalShow, setAccountDetailModalShow] = useState(false);
  const [chooseNetworkModalShow, setChooseNetworkModalShow] = useState(false);
  const currentNetwork = networkData.filter(e => parseInt(e.chainId) === chainId)[0];
  useEffect(() => {
    setNetwork(library.currentProvider.chainId);
  }, [library])

  return (
    <div className="d-flex align-items-center">
      <div className="Network border-0 d-flex align-items-center me-5" onClick={() => setChooseNetworkModalShow(true)}>
        {currentNetwork?.logo}&nbsp;
        {currentNetwork?.name}
      </div>
      <div className="WalletAddress d-flex align-items-center" onClick={() => setAccountDetailModalShow(true)}>
        <AddressSVG size={15} />
        <EllipsisText text={account} length={9} className="ms-2" />
      </div>
      {/* <div className="mx-3 Setting">
        <AiOutlineAlignLeft className="AlignIcon" size={28} />
      </div> */}
      {/* ---Modal--- */}
      <div>
        <AccountDetail show={accountDetailModalShow} onHide={() => setAccountDetailModalShow(false)} />
        <ChooseNetwork show={chooseNetworkModalShow} onHide={() => setChooseNetworkModalShow(false)} setNetwork={setNetwork} networkData={networkData.slice(0, 3)} />
      </div>
    </div>
  )
}
export default AfterLogin